import { ObjectID } from "../qCloudTemp/quickCloud";
import { FontOptions, IThemeSettings } from "./ITheme";
import { IIntegrationsSettings } from "./IntegrationSettings";

export interface IRequestData {
    url: string;
    body?: Record<string, string>;
    headers?: Record<string, string>;
}
export interface ISettingsExtended extends ISettings {
    appSettings: IPlateauUIObject;
}
export interface IPlateauUIOptions {
    corsEnabled?: boolean,
    disableLP?: boolean;
    iamWithPopup?: boolean;
    iamInPopup?: boolean;
    iam?: string | IRequestData | Promise<ISettingsIAM>;
    settings: string | IRequestData | ISettings | Promise<ISettings> | ISettingsExtended;
    asset?: string | IRequestData | ISettings | Promise<IAssetList>;
};

export interface IPlateauUIObject { value: string | Array<IPlateauUIObject> | Record<string, Array<IPlateauUIObject>> | any, type: contentType, format?: contentFormat };


export type contentType = "path" | "js" | "object" | "style" | "font";
export type contentFormat = "lottie" | "qjson";
export interface ISettings {
    settings: IPlateauUIObject,
    localProxy?: IPlateauUIObject,
    containerServices?: IPlateauUIObject,
    componentList?: IPlateauUIObject,
    pipeline?: IPlateauUIObject,
    alert?: IPlateauUIObject,
    loading?: IPlateauUIObject,
    loadingLottie?: IPlateauUIObject,
    globalLocalization?: IPlateauUIObject,
    rootqjson?: IPlateauUIObject,
    style?: IPlateauUIObject,
    font?: IPlateauUIObject,
    css?: IPlateauUIObject,
    container?: IPlateauUIObject,
}
export interface IInfo {
    buildTime: string;
}

export type IAssetList = Record<string, Record<string, Record<string, string | undefined>>>

export type ISettingsIAM = {
    iam?: {
        active: boolean;
        options?: ISettingsIAMOptions;
        unsecurePages?: string[];
        popup?: boolean;
    },
    iamV2?: ISettingsIAMV2
}
export interface ISettingsIAMOptions {

    identityProvider?: string;
    localeQueryStringKey?: string;
    subdomain?: string;
    locale?: string;

    preserveTokens?: boolean;
    //keycloak configuration parameters same as in keycloakjs. 
    url?: string;
    realm?: string;
    clientId?: string;
    onLoad?: 'login-required' | 'check-sso';
    pkceMethod?: 'S256';
    responseMode?: 'query' | 'fragment';
    /**
     * /path?query#fragment
     * @example http://b.com/path?a=5&b=6#z&hebe=3
     * @example /path?a=5&b=6#z&hebe=3
     * @example path?a=5&b=6
     * @example ?a=5&b=6
     * @example #a=5&b=6
     * 
     */
    redirectUri?: string;
    sessionStorageNamePrefix?: string;

}

export interface ISettingsIAMV2 {
    active: boolean,
    idendityProviderURL: string,
    clientId?: string,
    realm?: string,
    isRealmFromQS?: boolean,
    language?: string,
    isLangFromQS?: boolean,
    subdomainSource?: "constant" | "query" | "currentDomain",
    subdomainValue?: string,
    unsecurePages?: Array<{id: string, path: string}>,
    redirectUri?: string;
    onLoad: "login-required" | "check-sso";
    preserveTokens?: boolean;
    tokenEndPoint?: string;
    logoutEndpoint?: string;
    clientSecret?: string;
    popup?: boolean;
    sessionStorageNamePrefix?: string;   
}
export interface ISettingsLogging {
    minEngineLogLevel?: "Debug" | "Trace" | "Warning" | "Error";
    yamlLogType?: "Debug" | "Trace" | "Warning" | "Error";
    logBulkSize?: number;
}


export interface ISettingsLoading {
    webLoading?: string; //"<<lottie:1234>>"
    mobileLoading?: string; //"<<lottie:5678>>"
}

export interface ISettingsTagValues {
    tagValues: Array<ITagValue>;
}
export interface ITagValue {
    id: ObjectID; 
    label: string; 
    value: string; 
    definitionSource: TagDefinitionSource
}

export type TagType = { type: "freeText" } | { type: "preDefined", viewType: "combo" };

export type TagDefinitionSource= "orgGroup";

export interface IAppGroup {
    Settings?: string[];
    UserInterface?: string[];
    Connections?: string[];
    Other?: string[];
}
export interface IAppSettings {
    general: {
        siteTitle?: string;
        startScreen?: string; //"<<qjson:1234>>"
        webLoading?: string; //"<<lottie:1234>>"
        mobileLoading?: string; //"<<lottie:5678>>"
        containerServIntelli?: string; 
        componentList?:string;
        localProxy?:string;
        alert?:string;
        settings?:string;
        pipeline?:string;
        containerServices?:string;
        loading?: string;
    };
    oAuthSettings?: ISettingsIAMV2,
    themeSettings?: IThemeSettings,
    integrationsSettings?: IIntegrationsSettings,
    logging?: ISettingsLogging,
    loading?: ISettingsLoading,
    tagValues?: ISettingsTagValues;
    globalLocalization?:string;
}

